$primary-color: #65438f;
$secondary-color: #fdfdfe;
$text-color: #181718;
$secondary-text-color: #5e5d60;
$tertiary-text-color: #8e8b91;
$border-color: #d4d1d9;
$divider-color: #f2eff5;
$gap: 0.5rem;
$padding-y: 0.75rem;
$padding-x: 0;
$margin-y: 2rem;
$margin-x: 0;
$label-width-desktop: 28%;
$input-width-desktop: 65%;
$label-width-mobile: 100%;
$input-width-mobile: 100%;

.mvx-tab-content {
  .mvx-dynamic-form {
    .mvx-form-group {
      display: flex;
      align-items: start;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: $gap;
      padding: $padding-y $padding-x;

      .mvx-settings-form-label {
        width: $label-width-desktop;
        color: $text-color;
        word-wrap: break-word;

        p {
          font-size: 0.875rem;
          color: $text-color;
        }
      }

      .mvx-settings-input-content {
        width: $input-width-desktop;
        color: $secondary-text-color;

        .mvx-toggle-checkbox-content {
          margin-left: auto;
          position: relative;
          margin-bottom: 0.625rem;

          &::after {
            content: "";
            position: absolute;
            top: 0.55rem;
            left: 2.3rem;
            width: 0.25rem;
            height: 0.25rem;
            border: 0.0625rem solid $primary-color;
            border-radius: 8rem;
            transition: 0.3s;
            z-index: 0;
          }

          input {
            height: 0;
            width: 0;
            visibility: hidden;
            display: none;
          }

          label {
            cursor: pointer;
            width: 3.3rem;
            height: 1.5rem;
            background: 0 0;
            display: block;
            border-radius: 8rem;
            position: relative;
            border: 0.125rem solid $border-color;

            &::before {
              content: "";
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              left: 0.5rem;
              width: 0.17rem;
              height: 0.57rem;
              background: $secondary-color;
              transition: 0.3s;
            }
            &::after {
              content: "";
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              left: 0.313rem;
              width: 0.875rem;
              height: 0.875rem;
              background: $primary-color;
              border-radius: 90rem;
              transition: 0.3s;
              z-index: 1;
            }
          }
          input:checked + label {
            background: $primary-color;
            border: 0.125rem solid $primary-color;
          }
          input:checked + label:after {
            left: calc(100% - 1.313rem);
            transform: translateY(-50%);
            background-color: $secondary-color;
          }
        }

        input,
        textarea {
          width: 100%;
          border: 0.063rem solid $border-color;
        }
      }

      .mvx-settings-metabox-description {
        margin-top: 0.625rem;
        font-style: italic;
        font-size: 0.75rem;
        color: $tertiary-text-color;
      }
    }
  }

  .mvx-setting-section-divider {
    background-color: $divider-color;
    height: 0.05rem;
    margin: $margin-y $margin-x;
  }

  @media only screen and (max-width: 48.321rem) {
    .mvx-dynamic-form {
      .mvx-form-group {
        .mvx-settings-form-label {
          width: $label-width-mobile;
        }

        .mvx-settings-input-content {
          width: $input-width-mobile;
        }
      }
    }
    .mvx-setting-section-divider {
      margin: $margin-y $margin-x;
    }
  }
}
